.choices {
  width: 100%;

  &__inner {
    border-radius: 0px;
    border: 1px solid #0d3f88;
    background-image: linear-gradient(45deg, transparent 50%, white 50%), linear-gradient(135deg, white 50%, transparent 50%), linear-gradient(to right, #0c3e88, #0c3e88);
    background-position: calc(100% - 20px) calc(1em + 2px),calc(100% - 12px) calc(1em + 2px),100% 0;
    background-size: 8px 9px,8px 9px,3.1em 3.1em;
    background-repeat: no-repeat; }

  &__list--dropdown {

    input {
      height: 49px; } }

  &[data-type*=select-one]:after {
    border-color: #0c3e88 transparent transparent;
    right: 16px;
    margin-top: -6px; }

  &__list--dropdown .choices__list {
    max-height: 100px; } }

.is-open.is-focused .choices__inner {
  background-image: linear-gradient(135deg,transparent 50%,#fff 50%),linear-gradient(45deg,#fff 50%,transparent 50%),linear-gradient(to right,#0c3e88,#0c3e88);
  background-position: calc(100% - 15px) calc(1em - 7px),calc(100% - 8px) calc(1em - 7px),100% 0;
  background-size: 8px 9px,8px 9px,3em 3em; }
