.popup {
	$this: &;
	display: none;
	@include vcenter;
	position: fixed;
	width: 100vw;
	height: 100vh;
	background-color: $white;
	z-index: 10;
	&.is-show {
		display: block; }
	&.is-required {
		#{$this}__next {
			display: none; } }
	&__content {
		width: 60vw;
		height: 60vh;
		padding: 20px;
		background-color: $gray;
		@include vcenter;
		overflow-y: auto;
		p {
			display: flex;
			justify-content: space-between;
			padding: 5px 0;
			margin-bottom: 10px;
			span:last-child {
				color: red; } } }

	&__btns {
		position: absolute;
		top: 30px;
		right: 30px; }
	&__icon {
		position: absolute;
		top: 86px;
		left: 26%;
		transform: translateX(-50%); }
	&__title {
		position: absolute;
		top: 120px;
		left: 50%;
		transform: translateX(-50%); } }
