.tabs {
	position: relative;
	display: flex;
	width: 945px;
	height: 780px;
	margin-left: auto;
	box-shadow: 0px 4px 12px rgba($black, 0.25);
	border-radius: 5px 5px 5px 0;
	.tabs {
		flex-direction: column; }

	&__nav-title {
		display: flex;
		flex-wrap: wrap;
		width: 100%;
		margin: 0;
		list-style-type: none;
		z-index: 1;
		li {
			position: relative;
			display: flex;
			width: 25%;
			height: 70px;
			@include fontRM;
			font-size: 18px;
			text-align: center;
			border-right: 1px solid $white;
			background-color: $blue;
			a {
				display: flex;
				align-items: center;
				justify-content: center;
				width: 100%;
				height: 100%;
				padding: 0 35px;
				color: $white; }
			&:last-child {
				border-right: none;
				border-radius: 0 5px 0 0; }
			&:first-child {
				border-radius: 5px 0 0 0; }
			&.is-active {
				background-color: $white;
				a {
					color: $black;
					box-shadow: 6px -4px 4px rgba($black, 0.25); } }

			&.circle {
				&:before {
					content: '';
					position: absolute;
					top: 50%;
					left: 15px;
					transform: translateY(-50%);
					width: 16px;
					height: 16px;
					background-color: $brightGreen;
					border-radius: 50%; } } }

		&--left {
			position: absolute;
			left: -195px;
			bottom: 0;
			width: 195px;
			flex-direction: column;
			li {
				width: 100%;
				@include fontRR;
				font-size: 16px;
				line-height: 70px;
				border-right: none;
				border-bottom: 1px solid $white;
				&:last-child {
					border-radius: 0 0 0 5px;
					border-right: none; }
				&.is-active a {
					box-shadow: -4px 4px 4px rgba($black, 0.25);
					z-index: 1; }
				a {
					text-align: left;
					padding: 0 0 0 40px; } } }
		&--other {
			bottom: auto;
			top: 500px; }

		&--center li a {
			text-align: center;
			padding: 0; } }

	&__contents {
		display: flex;
		flex-direction: column;
		width: 100%;
		&--fix-height {
			height: 710px; } }
	&__content {
		height: 100%;
		height: 100%;
		display: none;
		&.is-active {
			display: block;
			width: 100%; } } }
