.input-file {
	position: relative;
	width: 100%;
	height: 44px;
	padding: 0 30px;
	@include fontRR;
	font-size: 18px;
	line-height: 44px;
	color: $gray;
	font-style: italic;
	background-color: $lightGray;
	border: 1px solid $blue;
	&:disabled {
		border-color: $gray; }
	&__input {
		display: none; }
	&:before {
		content: '';
		position: absolute;
		top: -1px;
		right: -1px;
		width: 44px;
		height: 44px;
		background-color: $blue; }
	&:after {
		@include fontRR;
		content: '• • •';
		position: absolute;
		top: -1px;
		right: -1px;
		width: 44px;
		text-align: center;
		font-size: 24px;
		color: $white; } }

.input {
	width: 100%;
	height: 40px;
	padding: 0 30px;
	@include fontRR;
	font-size: 18px;
	line-height: 40px;
	color: $gray;
	font-style: italic;
	background-color: $lightGray;
	border: 1px solid $blue;
	&:disabled {
		border-color: $gray; }
	@include placeholder {
		@include fontRR;
		font-size: 18px;
		line-height: 40px;
		color: $gray;
		font-style: italic; }

	&--white {
		background-color: $white;
		border-color: $gray; } }

.input-rect {
	width: 80px;
	height: 80px;
	background-color: $lightBlue;
	border: 1px solid $blue; }

.input-pass {
	position: relative;
	width: 100%;

	&__icon {
		position: absolute;
		top: 50%;
		right: 10px;
		transform: translateY(-50%); } }
