.members {
	$this: &;
	display: none;
	position: relative;
	min-height: calc(100vh - 125px);
	&.is-show {
		display: block; }

	&__tree {
		display: flex;
		flex-direction: column;
		&-row {
			display: flex;
			justify-content: center; } }

	&__btn-add {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100px;
		height: 100px;
		color: $white;
		font-size: 80px;
		z-index: 10;
		background-color: $green;
		border-radius: 5px;
		&:hover {
			box-shadow: 0 2px 34px 1px rgba($black,.19); } }
	&.is-empty {
		.members__btn-add {
			position: absolute;
			@include vcenter; } } }
