$lineWidth: 174px;

.member {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	flex-shrink: 0;
	width: 124px;
	height: 124px;
	padding: 6px;
	margin: 25px;
	@include fontRR;
	border-radius: 15px;
	background-color: $white;
	$this: &;
	&[data-first="true"] {
		#{$this}__progress {
			right: -25px; }
		&:after {
			content: '';
			position: absolute;
			top: -7px;
			left: -7px;
			width: 132px;
			height: 132px;
			border-radius: 20px;
			z-index: -1; } }
	&--man[data-first="true"]:after {
		border: 2px solid $blue; }
	&--woman[data-first="true"]:after {
		border: 2px solid $pink; }

	&--man {
		border: 3px solid $blue;
		box-shadow: 0px 4px 4px rgba($black, 0.25);
		#{$this}__close {
			background-color: $blue; } }
	&--woman {
		border: 3px solid $pink;
		box-shadow: 0px 4px 4px rgba($black, 0.25);
		#{$this}__close {
			background-color: $pink; } }

	&__close {
		position: absolute;
		top: -3px;
		right: -3px;
		width: 25px;
		height: 25px;
		border-radius: 50%;
		&:before, &:after {
			content: '';
			position: absolute;
			top: 5px;
			left: 11px;
			transform: rotate(45deg);
			width: 3px;
			height: 16px;
			background-color: $white; }
		&:after {
			transform: rotate(-45deg); }
		&:hover {
			opacity: 0.8; } }

	&__text {
		display: flex;
		justify-content: space-between;
		align-items: baseline;
		max-width: 50px;
		@include fontRR;
		font-size: 12px;
		text-align: left;
		line-height: 1.4;
		z-index: 5;
		span {
			font-size: 12px;
			margin-right: 5px; } }

	&__connection {
		position: relative;
		@include fontRB;
		text-align: left;
		z-index: 2; }

	&__icon {
		display: flex;
		@include vcenter;
		font-size: 70px;
		.icon-man {
			fill: $gray2; }
		.icon-woman {
			fill: $gray2; }
		&-gender {
			display: flex;
			position: absolute;
			bottom: 0;
			right: 0;
			font-size: 30px; } }

	&__progress {
		position: absolute;
		top: 0;
		right: -20px;
		display: flex;
		align-items: flex-end;
		width: 10px;
		height: 100%;
		border: 1px solid $gray;
		border-radius: 5px;
		pointer-events: none;
		span {
			width: 100%;
			background-color: $green; } }

	&.fake {
		border-color: transparent; }
	.line {
		display: none;
		position: absolute;
		width: $lineWidth;
		height: 10px;
		background-color: $blue;
		pointer-events: none; }

	.line-pair {
		display: none;
		position: absolute;
		pointer-events: none;
		width: 84px;
		height: 56px;
		font-size: 50px;
		z-index: -2; }

	&.up-long:before {
			content: '';
			position: absolute;
			top: -53px;
			left: 50%;
			transform: translateX(-50%);
			width: 10px;
			height: 50px;
			background-color: $blue; }

	&.down {
		&.pair-right .line-pair {
			display: block;
			right: -70px;
			bottom: -30px; }

		&.pair-left .line-pair {
			display: block;
			left: -70px;
			bottom: -30px; } }
	&.up {
		.line {
			top: -23px; }
		&:before {
			content: '';
			position: absolute;
			top: -23px;
			left: 50%;
			transform: translateX(-50%);
			width: 10px;
			height: 20px;
			background-color: $blue; }

		&.right .line {
			display: block;
			left: 50%;
			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				right: 0;
				transform: translateX(50%);
				width: 10px;
				height: 20px;
				background-color: $blue; } }

		&.right-parent-pair .line {
			display: block;
			width: $lineWidth/2;
			left: 50%;
			z-index: -1;

			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				right: 0;
				transform: translateX(50%);
				width: 10px;
				height: 20px;
				background-color: $blue; } }

		&.right2 .line {
			display: block;
			width: $lineWidth*2;
			left: 50%;
			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				right: 0;
				transform: translateX(50%);
				width: 10px;
				height: 20px;
				background-color: $blue; } }

		&.right-parent-pair2 .line {
			display: block;
			width: $lineWidth*2-$lineWidth/2;
			left: 50%;
			z-index: -1;

			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				right: 0;
				transform: translateX(50%);
				width: 10px;
				height: 20px;
				background-color: $blue; } }
		&.right3 .line {
			display: block;
			width: $lineWidth*3;
			left: 50%;
			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				right: 0;
				transform: translateX(50%);
				width: 10px;
				height: 20px;
				background-color: $blue; } }

		&.right-parent-pair3 .line {
			display: block;
			width: $lineWidth*3-$lineWidth/2;
			left: 50%;
			z-index: -1;

			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				right: 0;
				transform: translateX(50%);
				width: 10px;
				height: 20px;
				background-color: $blue; } }

		&.left .line {
			display: block;
			right: 50%;
			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				left: 0;
				transform: translateX(-50%);
				width: 10px;
				height: 40px;
				background-color: $blue; } }

		&.left-parent-pair .line {
			display: block;
			width: $lineWidth/2;
			right: 50%;
			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				left: 0;
				transform: translateX(-50%);
				width: 10px;
				height: 20px;
				background-color: $blue; } }

		&.left2 .line {
			display: block;
			right: 50%;
			width: $lineWidth*2;
			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				left: 0;
				transform: translateX(-50%);
				width: 10px;
				height: 20px;
				background-color: $blue; } }

		&.left-parent-pair2 .line {
			display: block;
			width: $lineWidth*2-$lineWidth/2;
			right: 50%;
			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				left: 0;
				transform: translateX(-50%);
				width: 10px;
				height: 20px;
				background-color: $blue; } }
		&.left3 .line {
			display: block;
			right: 50%;
			width: $lineWidth*3;
			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				left: 0;
				transform: translateX(-50%);
				width: 10px;
				height: 20px;
				background-color: $blue; } }

		&.left-parent-pair3 .line {
			display: block;
			width: $lineWidth*3-$lineWidth/2;
			right: 50%;
			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				left: 0;
				transform: translateX(-50%);
				width: 10px;
				height: 20px;
				background-color: $blue; } } } }
