.popup-check {
	$this: &;
	display: none;
	@include vcenter;
	position: fixed;
	width: 100vw;
	height: 100vh;
	background-color: rgba(0,0,0,.5);
	z-index: 10;
	&__container {
		position: absolute;
		top: 50%;
		left: 50%;
		-webkit-transform: translate(-50%,-50%);
		-ms-transform: translate(-50%,-50%);
		transform: translate(-50%,-50%);
		width: 800px;
		padding: 30px;
		background-color: #fff;
		border: 2px solid #0d3f88;
		border-radius: 5px; }
	&__title {
		width: 240px;
		font-family: 'Roboto-Medium',sans-serif;
		font-style: normal;
		font-weight: 400;
		font-size: 18px;
		text-transform: uppercase;
		text-align: center;
		margin: 0 auto; }
	&__close {
		position: absolute;
		top: 10px;
		right: 10px;
		display: flex;
		margin-left: auto;
		font-size: 0;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		background-color: $transp;
		&:before, &:after {
			content: '';
			position: absolute;
			top: 5px;
			left: 19px;
			transform: rotate(45deg);
			width: 2px;
			height: 30px;
			background-color: $blue; }
		&:after {
			transform: rotate(-45deg); }
		&:hover {
			background-color: rgba($blue, 0.2); } }
	&__anchor {
		position: relative; }
	&.is-show {
		display: block; }
	&__content {
		/*display: none*/
		/*width: 60vw*/
		/*height: 60vh*/
		padding: 20px;
		/*background-color: $gray*/
		/*+vcenter*/
		/*overflow-y: auto*/ }
	&__btns {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-pack: justify;
		-ms-flex-pack: justify;
		justify-content: space-between; }
	&__btn {
		display: none;
		width: auto;
		&.is-show {
			display: block; } }
	&__icon {
		position: absolute;
		top: 5px;
		right: 5px;
		/*transform: translateX(-50%)*/ } }
