.form-household {
	$this: &;
	margin-bottom: 30px;

	&__wrap {
		padding: 50px; }

	&__flex {
		display: flex;
		justify-content: space-between;
		align-items: center;
		&--center {
			justify-content: center;
			margin: 0 auto; }
		&--gray {
			background-color: $gray2; }
		&--column {
			flex-direction: column;
			align-items: flex-start; } }

	&__el {
		display: flex;
		justify-content: space-between;
		align-items: center;
		&-title {
			min-width: 90px;
			@include fontRM;
			font-size: 18px;
			text-transform: uppercase; } }

	&__row {}


	&__col {
		width: 48%; }

	&__subtitle {
		@include fontRB;
		font-size: 24px;
		text-align: center;
		margin-bottom: 30px; }

	&__info {
		@include fontRR;
		font-size: 16px;
		color: $blue;
		font-style: italic;
		&--border {
			font-size: 18px;
			padding: 20px 0;
			margin: 30px 0;
			border-top: 1px solid $blue;
			border-bottom: 1px solid $blue; } }

	&__popup {
		position: relative;
		padding: 30px 0;
		#{$this}__flex {
			padding: 15px 30px; }
		#{$this}__subtitle {
			margin: 0 auto 10px;
			font-size: 18px; }
		#{$this}__el {
			margin-bottom: 10px;
			&:last-child {
				margin-bottom: 0; } }
		#{$this}__col {
			width: auto;
			margin-right: 30px;
			&:last-child {
				margin-right: 0; } } }
	&__icon {
		position: absolute;
		top: 15px;
		right: 15px;
		display: flex;
		width: 30px;
		height: 30px;
		cursor: pointer;
		svg {
			width: 100%;
			height: 100%; } }

	&__title {
		@include fontRB;
		font-size: 24px;
		text-align: center;
		text-transform: uppercase; }

	&__btns {
		display: flex;
		justify-content: space-between;
		padding: 0 30px;
		button {
			width: 330px; } } }
