.radio-num {
	$this: &;
	position: relative;
	display: flex;

	&__input {
		display: none; }

	&__label {
		width: 80px;
		height: 80px;
		@include fontRB;
		font-size: 48px;
		line-height: 80px;
		text-align: center;
		color: $white;
		cursor: pointer;
		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			height: 80px;
			width: 80px;
			background-color: $blue;
			border-radius: 50%;
			z-index: -1; } }


	&__input:checked + &__label {
		width: 100px;
		height: 100px;
		font-size: 72px;
		line-height: 100px;
		color: $black;
		&:before {
			width: 100px;
			height: 100px;
			background-color: $white;
			border: 2px solid $blue;
			box-shadow: 0px 4px 4px rgba($black, 0.25); } }

	&--small {
		#{$this}__label {
			width: 67px;
			height: 67px;
			font-size: 36px;
			line-height: 67px;
			&:before {
				width: 67px;
				height: 67px; } }
		#{$this}__input:checked + #{$this}__label {
			width: 84px;
			height: 84px;
			font-size: 48px;
			line-height: 84px;
			color: $black;
			&:before {
				width: 84px;
				height: 84px; } } } }
