.form-room {
	margin-bottom: 30px;

	&__inner {
		padding: 30px;
		height: 100%; }

	&__flex {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		&--column {
			flex-direction: column;
			height: 100%; }
		&--center {
			align-items: center; } }

	&__col {
		width: 48.5%;
		margin-bottom: 20px; }

	&__el {
		margin-bottom: 5px;
		&:last-child {
			margin-bottom: 0; }
		&--mb-big {
			margin-bottom: 40px; }
		&--mb-medium {
			margin-bottom: 20px; } }

	&__title {
		width: 100%;
		@include fontRB;
		font-size: 24px;
		text-align: center;
		margin-bottom: 30px; }

	&__subtitle {
		display: flex;
		align-items: center;
		height: 60px;
		padding: 0 20px;
		margin-bottom: 20px;
		@include fontRM;
		background-color: $lightBlue;
		&--transp {
			background-color: $transp;
			padding: 0;
			margin: 0; } }

	&__el-info {
		display: flex;
		flex-grow: 1; }

	&__info {
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding: 0 0 0 25px;
		@include fontRR;
		font-size: 16px;
		font-style: italic;
		color: $blue;
		border-left: 1px solid $blue;
		p, ul {
			margin-bottom: 20px;
			&:last-child {
				margin-bottom: 0; } }
		li {
			position: relative;
			padding-left: 20px;
			&:after {
				content: '';
				position: absolute;
				left: 0;
				top: 10px;
				width: 6px;
				height: 6px;
				background-color: $blue;
				border-radius: 50%; } } }

	&__input {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		.input {
			width: 150px;
			margin-bottom: 15px; }
		&-title {
			@include fontRB;
			font-size: 18px; } } }
