.btn-blue-icon {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	padding: 10px 30px;
	@include fontRB;
	font-size: 18px;
	color: $white;
	text-transform: uppercase;
	background-color: $blue;
	border-radius: 5px;
	box-shadow: 0 2px 34px 1px rgba($black, 0.4);
	&__icon {
		display: flex;
		position: absolute;
		top: 50%;
		right: 10px;
		transform: translateY(-50%);
		&--left {
			right: auto;
			left: 10px;
			transform: translateY(-50%) rotate(180deg); } }
	&:hover {
		background-color: $blueHover; }

	&.disabled {
		pointer-events: none;
		background-color: $gray; } }

.btn-icon {
	display: flex;
	align-items: center;
	width: 100%;
	padding: 10px;
	@include fontRB;
	font-size: 14px;
	text-transform: uppercase;
	color: $blue;
	background-color: $white;
	border: 1px solid $blue;
	border-radius: 5px;
	box-shadow: 0 2px 34px 1px rgba($black, 0.4);
	&__icon {
		height: 30px;
		width: 30px;
		margin-right: 15px;
		img, svg {
			width: 100%;
			height: 100%; } }
	&:hover {
		opacity: 0.8; } }
