.root[data-section="auth"] {

	.user-block {
		position: relative;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		margin-bottom: 30px;

		&__room {
			display: none; }

		&__btn {
			width: 165px;
			height: 50px;
			margin: 0;
			&[data="save"] {
				display: none; } }

		&__title {
			display: block;
			@include vcenter;
			@include fontRB;
			font-size: 36px;
			color: $blue;
			text-transform: uppercase; } } }

.user-block {
	position: absolute;
	display: flex;
	flex-direction: column;
	margin-bottom: 30px;

	&__btn {
		width: 165px;
		height: 50px;
		margin-bottom: 30px;
		&:last-of-type {
			margin-bottom: 0;
			margin-top: 70px; } }

	&__title {
		display: none;
		@include vcenter;
		@include fontRB;
		font-size: 36px;
		color: $blue;
		text-transform: uppercase; }

	&__room {
		position: absolute;
		bottom: -195px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		width: 165px;
		height: 165px;
		padding: 10px 10px 20px;
		border: 1px solid $blue;
		border-radius: 5px;
		text-align: center;
		&-title {
			@include fontRM;
			font-size: 18px;
			text-transform: uppercase; }
		&-text {
			@include fontRR;
			font-size: 15px; } } }

.root[data-section="new-section-member"] {
	.user-block {
		margin-bottom: 0;
		&__btn:last-of-type {
			margin-top: 0;
			margin-bottom: 30px; }
		&__room {
			position: static;
			height: 50px;
			overflow: hidden; } } }
