.form-member {
	margin-bottom: 30px;
	$this: &;

	&__inner {
		padding: 30px;
		height: 100%; }

	&__flex {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		&--column {
			flex-direction: column;
			height: 100%; }
		&--center {
			align-items: center; }
		&--start {
			justify-content: flex-start; }
		&--space-around {
			justify-content: space-around;
			margin-bottom: 55px; } }

	&__wrap {
		margin-left: 30px; }

	&__row {
		position: relative;
		&--mb {
			margin-bottom: 20px; }
		&--mb-big {
			margin-bottom: 40px; } }

	&__col {
		position: relative;
		width: 48.5%;
		margin-bottom: 20px;
		$col: &;
		&--absolute {
			position: absolute;
			bottom: -100px;
			right: 0;
			width: 48.5%;
			border: 1px solid $blue;
			#{$col} {
				width: 100%; }
			&-a8 {
				border: none;
				width: calc(100% - 50px);
				bottom: -310px;
				#{$this}__wrap {
					margin-bottom: 10px; } } }

		&--mb-big {
			margin-bottom: 40px; }
		&--large {
			width: 90%;
			margin-left: auto; }
		&--medium {
			width: 70%; }
		&--medium-right {
			width: 70%;
			margin-left: auto; } }

	&__el {
		margin-bottom: 5px;
		&:last-child {
			margin-bottom: 0; }
		&--mb-big {
			margin-bottom: 40px; }
		&--mb-medium {
			margin-bottom: 15px; }
		&--absolute {
			position: absolute;
			left: 50px;
			top: 200px; } }

	&__title {
		width: 100%;
		@include fontRB;
		font-size: 24px;
		text-align: center;
		margin-bottom: 30px;
		&--left {
			text-align: left; }
		&--medium {
			max-width: 650px;
			margin: 0 auto 20px; }
		&--no-mb {
			margin-bottom: 0; } }

	&__subtitle {
		display: flex;
		align-items: center;
		height: 60px;
		padding: 0 20px;
		margin-bottom: 20px;
		@include fontRM;
		background-color: $lightBlue;
		&--transp {
			background-color: $transp;
			padding: 0;
			margin: 0; }
		&--italic {
			display: block;
			min-height: 100px;
			height: auto;
			padding: 10px 20px;
			i {
				@include fontRR;
				font-style: italic; } }

		&--big {
			height: 90px; } }

	&__text {
		margin-bottom: 10px;
		&--bold {
			@include fontRB; } }

	&__el-info {
		display: flex;
		flex-grow: 1; }

	&__icon-tooltip {
		position: absolute;
		font-size: 40px;
		&--a1 {
			left: -70px;
			top: 35px; }
		&--a7 {
			font-size: 57px;
			left: -50px;
			top: -75px; }
		&--a8 {
			transform: rotate(90deg);
			top: -65px;
			left: -15px; }
		&--a12 {
			transform: rotate(90deg);
			top: -60px;
			left: 110px; }
		&--a14-yes {
			transform: rotate(90deg);
			top: -55px;
			left: 200px; }
		&--a14-no {
			transform: rotate(90deg);
			top: -155px;
			right: 200px; }
		&--a15-1 {
			transform: rotate(90deg);
			top: -55px;
			right: 200px; }
		&--a15-2 {
			transform: rotate(90deg);
			top: -50px;
			left: 45px; }
		&--a18 {
			transform: rotate(90deg);
			top: -55px;
			left: 200px; }
		&--a19 {
			transform: rotate(90deg);
			top: -55px;
			left: 200px; }
		&--a20 {
			transform: rotate(90deg);
			top: -55px;
			right: 200px; } }
	&__info {
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding: 0 0 0 25px;
		@include fontRR;
		font-size: 16px;
		font-style: italic;
		color: $blue;
		border-left: 1px solid $blue;
		p, ul {
			margin-bottom: 20px;
			&:last-child {
				margin-bottom: 0; } }
		li {
			position: relative;
			padding-left: 20px;
			&:after {
				content: '';
				position: absolute;
				left: 0;
				top: 10px;
				width: 6px;
				height: 6px;
				background-color: $blue;
				border-radius: 50%; } }

		&--top-bot {
			padding: 20px 0;
			margin-bottom: 20px;
			border-left: none;
			border-top: 1px solid $blue;
			border-bottom: 1px solid $blue;
			p {
				margin-bottom: 0; } } }

	&__input {
		padding: 0 30px;

		&--center {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			.input {
				width: 150px; } } }

	&__select {
		// padding: 0 30px
		margin-bottom: 20px; }


	&__livelihood {
		display: flex;
		flex-direction: column;
		height: 100%;

		&-wrap {
			display: flex;
			flex-grow: 1; }

		&-title {
			width: 200px;
			margin-left: auto;
			margin-bottom: 20px;
			@include fontRM;
			font-size: 18px;
			color: $blue;
			text-align: right; }

		&-left {
			display: flex;
			flex-direction: column;
			width: 300px;
			padding-right: 10px;
			border-right: 1px solid $blue; }

		&-right {
			display: flex;
			flex-direction: column;
			padding-top: 67px;
			padding-left: 10px; }

		&-radio {
			position: relative;
			width: 16px;
			height: 16px;
			margin-left: auto;
			margin-bottom: 19px;
			&:after {
				content: '';
				position: absolute;
				bottom: -15px;
				left: -1px;
				width: 60px;
				height: 1px;
				background-color: $blue; }
			&:last-child:after {
				display: none; } }


		&-check {
			margin-bottom: 15px; } }

	&__job-region {
		position: relative;
		margin-top: 70px;
		padding-left: 110px;

		.form-member__text {
			font-size: 18px; }
		.form-member__subtitle {
			width: 230px;
			height: auto;
			text-transform: uppercase; }
		.form-member__select {
			flex-grow: 1;
			margin-bottom: 0;
			padding: 0; }
		.form-member__flex {
			margin-bottom: 20px; }
		.form-member__el {
			width: 50%;
			&:last-child {
				margin-bottom: 0; } } }

	&__migration {
		position: relative;
		.form-member__subtitle {
			width: 230px;
			height: auto;
			text-transform: uppercase; }
		.form-member__select {
			flex-grow: 1;
			margin-bottom: 0;
			padding: 0; }
		.form-member__flex {
			margin-bottom: 20px; } } }
