.select {
	width: 100%;
	height: 40px;
	padding: 0 30px;
	@include fontRR;
	font-size: 18px;
	line-height: 40px;
	color: $gray;
	font-style: italic;
	background-color: $lightGray;
	border: 1px solid $blue;
	&:disabled {
		border-color: $gray; }

	&--white {
		background-color: $white;
		border-color: $gray; }

	&--big {
		height: 85px; } }

