body {
  font-family: 'Roboto-Regular', sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 1.3;
}

a {
  text-decoration: none;
}

.container {
  max-width: 1140px;
  width: 100%;
  margin: 0 auto;
  position: relative;
}

[hidden] {
  display: none;
}

.radio {
  position: relative;
  padding-left: 30px;
}

.radio__input {
  opacity: 0;
  position: absolute;
  bottom: 20px;
  right: 0;
  z-index: -1;
}

.radio__label {
  cursor: pointer;
}

.radio__label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 4px;
  height: 16px;
  width: 16px;
  border: 2px solid #979797;
  border-radius: 50%;
}

.radio__label:after {
  display: none;
  content: '';
  position: absolute;
  left: 4px;
  top: 8px;
  height: 8px;
  width: 8px;
  background-color: #0d3f88;
  border-radius: 50%;
}

.radio__input:checked + .radio__label:after {
  display: block;
}

.radio__input:checked + .radio__label:before {
  border-color: #0d3f88;
}

.radio--top {
  font-family: 'Roboto-Medium', sans-serif;
  font-style: normal;
  font-weight: normal;
  padding-left: 0;
  padding-top: 25px;
}

.radio--top .radio__label:before, .radio--top .radio__label:after {
  left: 50%;
  transform: translateX(-50%);
}

.check {
  position: relative;
  padding-left: 30px;
}

.check__input {
  display: none;
}

.check__label {
  cursor: pointer;
}

.check__label:before {
  content: '';
  position: absolute;
  left: 0;
  top: 4px;
  height: 16px;
  width: 16px;
  border: 2px solid #979797;
  border-radius: 3px;
}

.check__label:after {
  display: none;
  content: '';
  position: absolute;
  left: 4px;
  top: 8px;
  height: 8px;
  width: 8px;
  background-color: #0d3f88;
  border-radius: 2px;
}

.check__input:checked + .check__label:after {
  display: block;
}

.check__input:checked + .check__label:before {
  border-color: #0d3f88;
}

