.modal {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 20;
	&.is-show {
		display: block; }
	&__overlay {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba($black, 0.5); }

	&__content {
		@include vcenter;
		width: 800px;
		background-color: $white;
		border: 2px solid $blue;
		border-radius: 5px; } }
